/* Styles for outer nav wrapper */
.outer-nav-wrapper-nav {
    display: flex;
    flex-wrap: wrap;
    /* Allow items to wrap to the next line if needed */
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    /* Dark background */
    padding: 10px 20px;
}

/* Styles for searchbar */
.searchbar-div {
    flex: 1;
    /* Take up remaining space */
    display: flex;
    /* Add flex display to allow children to align properly */
    align-items: center;
    /* Align items vertically */
}

/* Styles for navbar */
.navbar {
    display: flex;
    align-items: center;
    margin-top: 10px;
    /* Add margin to separate navbar from search bar */
}

/* Styles for links */
.link {
    position: relative;
    text-decoration: none;
    color: #fff;
    /* White color for links */
    padding: 10px;
    border-radius: 5px;
    transition: background-color 0.3s ease, cursor 0.3s ease;
    /* Smooth transition */
}

.link:hover {
    background-color: #555;
    /* Darker background on hover */
    cursor: pointer;
    /* Pointer cursor on hover */
}

/* Styles for user dropdown */
.user-dropdown-outer-div {
    position: relative;
}

/* Styles for username display */
.username-display {
    text-decoration: none;
    color: #fff;
    /* White color for links */
    padding: 10px;
    border-radius: 5px;
    transition: background-color 0.3s ease, cursor 0.3s ease;
}

.username-display:hover {
    background-color: #555;
    /* Darker background on hover */
    cursor: pointer;
    /* Pointer cursor on hover */
}

/* Styles for username dropdown arrow */
.username-dropdown-arrow {
    margin-left: 5px;
}

/* Styles for user links dropdown */
.user-links {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #333;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
}

.user-links.active {
    margin-top: 12px;
    display: flex;
    /* Show dropdown when active */
    flex-direction: column;
}

/* Styles for cart badge */
.cart-badge {
    position: absolute;
    top: 50%;
    right: -10px;
    /* Adjust this value as needed */
    transform: translateY(-50%);
    background-color: #dc3545;
    /* Red background */
    color: #fff;
    padding: 3px 6px;
    border-radius: 50%;
    font-size: 12px;
}

/* ========================================================================================================= */

/* Styles for search input */
.search-input {
    padding: 10px;
    width: 100%;
    /* Make the search input take up full width */
    max-width: 300px;
    border-radius: 5px;
    /* Adjust border radius as needed */
    outline: none;
}

/* Styles for search button */
.search-button {
    padding: 10px 20px;
    margin-left: 5px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
}

/* Styles for search button hover */
.search-button:hover {
    background-color: #0056b3;
}


